<template>
  <div class="panel border-1px-b" @click="select(item)" :class="[pageType]">
    <div class="text">
      <p class="title">{{item.title}}</p>
      <span class="time">{{item.timestamp}}</span>
    </div>
    <div class="cover" v-show="item.imgurl">
      <img :src="item.imgurl" alt="cover">
    </div>
  </div>
</template>

<script type="ecmascript-6">
export default {
  props: {
    pageType: {
      type: String,
      default: 'news'
    },
    item: {
      type: Object,
      required: true
    }
  },
  methods: {
    select(item) {
      this.$emit('select', item);
    }
  }
}
</script>

<style lang="stylus" scoped>
@import "~@/common/stylus/1px";

.panel
  display flex
  justify-content space-between
  min-height 1.5rem
  padding 0.2rem 0
  font-size 0
  &.notice .text .title
    max-width 100%
  border-1px-b(#ccc)
  &:last-child::before
    display none
  .text
    width 100%
    display flex
    flex-direction column
    justify-content space-between
    .title
      max-width 5.8rem
      white-space nowrap
      overflow hidden
      text-overflow ellipsis
      font-size 0.34rem
      color #000
    .time
      font-size 0.3rem
      color #777
  .cover
    flex-shrink 0
    img
      width 1rem
      height 1rem
      background-color #ccc
</style>




