<template>
  <scroll class="scroll-wrapper" ref="scroll"
          :pullup="true"
          :data="newsList"
          @scrollToEnd="getMore"
  >
    <div class="scroll-content">
       <transition-group name="fade-slide-up" tag="div">
          <panel v-for="item in newsList"
            :key="item.id"
            :item="item"
            @select="selectItem"
          />
        </transition-group>
      <load-more :over="over" :is-loading="isLoading"></load-more>
    </div>
  </scroll>
</template>

<script type="ecmascript-6">
import { getNewsList } from '@/api/news';
import { AID, ERR_OK } from '@/api/config';

import Scroll from '@/base/scroll/scroll';
import Panel from '@/base/panel/news-notice-list.vue';
import LoadMore from '@/base/load-more/load-more';

export default {
  data() {
    return {
      over: false,
      isLoading: false,
      newsList: []
    }
  },
  activated() {
    setTimeout(function() {
      this.$refs.scroll.refresh()
    }.bind(this), 20)
  },
  created() {
    this.limit = 10;
    this.offset = 0;

    this._getList();
  },
  methods: {
    _getList() {
      this.isLoading = true;

      const type = this.$route.params.type;
      const limit = this.limit;
      const offset = this.offset;

      getNewsList({ type, limit, offset }).then((res) => {
        if (res.data.code === ERR_OK) {
          this.newsList.push(...res.data.data.list);

          if (res.data.data.list.length < this.limit) {
            this.over = true;
          }

          this.isLoading = false;
        } else {
          this.$dialog.alert({
            mes: res.data.msg,
            callback() {}
          });
        }
      }).catch((err) => {
        console.error(err);
      });
    },

    getMore() {
      if (this.over) {
        return;
      }

      this.offset += 10;

      this._getList();
    },

    selectItem(item) {
      const id = item.id;

      this.$router.push(`/news/${id}/detail/1/aid/${AID}`);
    }
  },
  components: {
    Scroll,
    Panel,
    LoadMore
  }
}
</script>

<style lang="stylus" scoped>
@import "~@/common/stylus/mixin";

animation-fade-slide-up()

.scroll-wrapper
  position absolute
  width 100%
  height 100%
  background-color #fff
  overflow hidden
  font-size 0
  .scroll-content
    padding 0 0.3rem

</style>



